<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('RejectRequest') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ResignationItem: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Reason'),
                            name: "Reason",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created: async function () {
    },
    methods: {
        Close: function () {
            this.$parent.showRejectRequestModal = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                ResignationId: vm.ResignationItem.RESIGNATION_ID == null ? 0 : vm.ResignationItem.RESIGNATION_ID,
                EffectiveFrom: '',
                Reason: formfields.Reason,
                Status: "Resignation_Rejected"
            };
            var params = JSON.stringify(jsonobj);
            DataServices.ResignationStatus(params).then((response) => {
                if (response.data.msg== 'success') {
                    vm.ShowAlert(vm.$t("RejectedSuccess", [vm.$t("Resignation")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resignation")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>